import './global.css';

const addListener = (element, event, listener) => {
    element.addEventListener(event, listener, false);
};

const removeListener = (element, event, listener) => {
    element.removeEventListener(event, listener);
};






const switchButton = document.getElementById("switch-button");

const nightLabel = "Switch to Day Mode";
const dayLabel = "Switch to Night Mode";
let night = false;
switchButton.textContent=dayLabel

addListener(switchButton, 'click', () => {
    if (night) switchToDay(); else switchToNight();
});

const switchToNight = () => {
    switchButton.textContent=nightLabel;
    document.body.className="night";
    night = true;
};

const switchToDay = () => {
    switchButton.textContent=dayLabel;
    document.body.className="";
    night = false;
};